import './Settings.css';

import React, {Component} from 'react';
import {List, ListItem} from 'material-ui/List';
import Divider from 'material-ui/Divider';
const firebase = window.firebase;

const listStyle = {
  textAlign: 'left',
};

const listItemStyle = {
  backgroundColor: '#fff',
  zIndex: -1
};

const dividerStyle = {
  boxShadow: 'rgba(0, 0, 0, 0.16) 0px 3px 10px, rgba(0, 0, 0, 0.23) 0px 3px 10px'
};

export default class Settings extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      phone: '',
      email: '',
      class: '',
      major: '',
      photoURL: '',
    }
  }

  componentWillMount() {
   
  }

  render() {
    return (
      <div>
        <img className="user-photo" src={this.props.state.photoURL} />
        <List style={listStyle}>
          <Divider />
          <ListItem
            innerDivStyle={listItemStyle}
            primaryText="Name"
            secondaryText={this.props.state.name}
            leftIcon={
              <i className="icon-user settings-icon"></i>
            }
          />
          <Divider inset={true} />
          <ListItem
            innerDivStyle={listItemStyle}
            primaryText="Phone Number"
            secondaryText={this.props.state.phone}
            leftIcon={
              <i className="icon-phone settings-icon"></i>
            }
          />
          <Divider inset={true} />
          <ListItem
            innerDivStyle={listItemStyle}
            primaryText="Email Address"
            secondaryText={this.props.state.email}
            leftIcon={
              <i className="icon-mail-alt settings-icon"></i>
            }
          />
          <Divider inset={true} />
          <ListItem
            innerDivStyle={listItemStyle}
            primaryText="Class"
            secondaryText={this.props.state.class}
            leftIcon={
              <i className="icon-users settings-icon"></i>
            }
          />
          <Divider inset={true} />
          <ListItem
            innerDivStyle={listItemStyle}
            primaryText="Major"
            secondaryText={this.props.state.major}
            leftIcon={
              <i className="icon-briefcase settings-icon"></i>
            }
          />
          <Divider style={dividerStyle} />
        </List>
      </div>
    )
  }
}


